import { Component, ViewChildren, QueryList } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides } from 'ionic-angular';

/**
 * Generated class for the CashlineHistoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-cashline-history',
  templateUrl: 'cashline-history.html',
})
export class CashlineHistoryPage {
  @ViewChildren(Slides) private slides1: QueryList<Slides>;
  list_tab = [
    {
      id: '0',
      name: 'Diajukan',
      data: [
        {
          data: 'diajukan 1'
        },
        {
          data: 'diajukan 2'
        },
      ]
    },
    {
      id: '1',
      name: 'Berjalan',
      data: [
        {
          data: 'Berjalan 1'
        }
      ]
    },
    {
      id: '2',
      name: 'Selesai',
      data: [
        {
          data: 'Selesai 1'
        },
        {
          data: 'Selesai 2'
        },
        {
          data: 'Selesai 3'
        }
      ]
    }
  ]

  activeTab = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CashlineHistoryPage');
    this.slides1.toArray()[1].lockSwipes(true);
  }

  selectTab(id) {
    this.activeTab = id;
    this.slides1.toArray()[0].slideTo(id, 500);
  }

  slideChanged() {
    let currentIndex = this.slides1.toArray()[0].getActiveIndex();
    if (currentIndex == 2) {
      this.slides1.toArray()[0].lockSwipeToNext(true);
    } else {
      this.slides1.toArray()[0].lockSwipeToNext(false);
    }

    this.activeTab = currentIndex;
  }

}



